export class Producto {
	nombre: string;
	precio: number;
	id: number; // index 
	order: number

	constructor(id: number, nombre: string, precio: number, order: number){
		this.id = id;
		this.nombre = nombre;

		// if price is less than zero then we treat them all as null
		if (precio >= 0) {
			this.precio = precio;
		}

		if (!order){
			order = -1;
		}
		this.order = order;
	}

	public getRowForCatalogs() : Array<any> {
		var row = new Array();
		row.push(this.id);
		row.push(this.nombre);
		row.push(this.precio);
		row.push(this.order);
		return row;
	}

	static createEmptyProduct() : Producto{
		return new Producto(-1, "",  -1, -1);
	}
}