
export class Gasto {

    public id: number;
	public concepto: string;
    public recurrencia: string;
    public fechaPagado: Date;
    public montoReferencia: Number;
    public montoPagado: Number;
    public recurrente: boolean;

	constructor(id: number, recurrente: boolean, concepto: string, recurrencia: string, fechaPagado: Date, montoReferencia: Number, montoPagado: Number){
        this.id = id;
        this.recurrente = recurrente;
        this.concepto = concepto;
        this.recurrencia = recurrencia;
        this.fechaPagado = fechaPagado;
        this.montoReferencia = montoReferencia;
        this.montoPagado = montoPagado;
        console.log(montoPagado);
	}

    public getRowForGastos(){
        var row = new Array();
        row.push(this.id);
        row.push(this.concepto);
        if (this.recurrente) {
            if (!this.recurrencia) {
                this.recurrencia = "Todos los " + this.fechaPagado.getDate() + " de cada mes";
                row.push(this.recurrencia);
            }
            if (this.recurrente) row.push(this.montoReferencia);
        };
        row.push(this.fechaPagado);
        row.push(this.montoPagado);
        return row;
    }

    public static createEmptyGasto(): Gasto {
        return new Gasto(-1, false, "", null, null, null, null);
    }
}