import { Component, OnInit } from '@angular/core';

import { GapiSession } from '../../infrastructure/sessions/gapi.session';
import { AppComponent } from '../app.component'
import { Gasto } from '../../models/gasto.model'
import { NgZone } from '@angular/core';
import { Observable } from "rxjs";
import { from } from 'rxjs';
import {FormControl} from '@angular/forms';
import {startWith, map} from 'rxjs/operators';

@Component({
  selector: 'app-gastos-component',
  templateUrl: './gastos-component.component.html',
  styleUrls: ['./gastos-component.component.css']
})
export class GastosComponentComponent implements OnInit {

  private static rangeGastosRecurrentes = 'ControlGastosRecurrentes!A2:G';
  private static rangeGastosRecurrentesAddRow = 'ControlGastosRecurrentes!A';
  private static rangeGastosOcasionales = 'ControlGastosOcasionales!A2:D';
  private static rangeGastosOcasionalesAddRow = 'ControlGastosOcasionales!A';

  public allGastos: Array<Gasto> = [];
  private gastosRecurrentes: Array<Gasto> = [];
  private gastosOcasionales: Array<Gasto> = [];
  private manualGasto = Gasto.createEmptyGasto();

  control = new FormControl(); //used for the autocomplete text
	filteredGastos: Observable<Gasto[]>;

  constructor(
    private gapiSession: GapiSession,
		private ngZone: NgZone
  ) { }

  ngOnInit() {
    if (!this.gapiSession.isSignedIn){
			// use this to refresh the credentials
			//this.gapiSession.signOut();
			this.gapiSession.signIn();
		}

		this._loadGastosRecurrentes().subscribe( 
			gastosRange =>
			{
				// necessary to run insice the zone in order to fire the change detection
				this.ngZone.run( () =>
					{
						var listaGastos = gastosRange.result["values"];
						if (listaGastos){
							for(let i = 0; i < listaGastos.length; i++){

								var gastoFromSheet = listaGastos[i];
                var gasto = new Gasto(gastoFromSheet[0], true, gastoFromSheet[1],  gastoFromSheet[2], gastoFromSheet[4], gastoFromSheet[3], gastoFromSheet[5]);
                this.gastosRecurrentes.push(gasto);
                this.allGastos.push(gasto);
							}
						}
					}
				)
			}	
		);

		this._loadGastosOcasionales().subscribe( 
			gastosRange =>
			{
				// necessary to run insice the zone in order to fire the change detection
				this.ngZone.run( () =>
					{
						var listaGastos = gastosRange.result["values"];
						if (listaGastos){
							for(let i = 0; i < listaGastos.length; i++){

								var gastoFromSheet = listaGastos[i];

                // 0 = id, 1 = Concepto, 2 = Fecha Pagado, 3 = Monto Pagado
                var gasto = new Gasto(gastoFromSheet[0], false, gastoFromSheet[1], null, gastoFromSheet[2], null, gastoFromSheet[3]);
                this.gastosOcasionales.push(gasto);
                this.allGastos.push(gasto);
							}
						}
					}
				)
			}	
		);

    // the value in control is an string while the user is typing, once the users selects a product from the list, it will be a DetalleOrden
		this.filteredGastos = this.control.valueChanges.pipe(
			startWith(''),
			map(display => display ? this._filter(display instanceof Gasto ? display.concepto : display) : this.allGastos.slice())
		);
  }
  
  private _loadGastosRecurrentes(): Observable<any>{
		return from(
			gapi.client.request(
				{
					method: 'GET',
					// Returns a range of values from a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/get
					path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}/values/${GastosComponentComponent.rangeGastosRecurrentes}?valueRenderOption=${AppComponent.valueRenderOption}&dateTimeRenderOption=${AppComponent.dateTimeRenderOption}`
				}
			));
  }

  private _loadGastosOcasionales(): Observable<any>{
		return from(
			gapi.client.request(
				{
					method: 'GET',
					// Returns a range of values from a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/get
					path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}/values/${GastosComponentComponent.rangeGastosOcasionales}?valueRenderOption=${AppComponent.valueRenderOption}&dateTimeRenderOption=${AppComponent.dateTimeRenderOption}`
				}
			));
  }

  private _filter(value: string): Gasto[] {
		if (value){
			const filterValue = this._normalizeValue(value);
			const gastosMatched = this.allGastos
				.filter( g => this._normalizeValue(g.concepto).includes(filterValue));
			return gastosMatched;
		}

		return [];
	}

	private _normalizeValue(value: string): string {
		return value ? value.toLowerCase().replace(/\s/g, '') : "";
	}

	public displayFn(gasto?: Gasto): string | undefined {
		return gasto ? gasto.concepto : undefined;
	}

	public onConceptoSelected(gastoSelected: Gasto): void{
		this.manualGasto = gastoSelected;
  }
  
  public addGastoToTable() : void{

    // first lets validate the data
		if (!this.control.value || 
      this.manualGasto.montoPagado <= 0) {
      return;
    }

    this.manualGasto.fechaPagado = new Date();
    if (this.manualGasto.id <= 0){
			// if it gets here, means the gasto has not been registered so we need to add it into the ControlGastosRecurrentes or ControlGastosOcasionales catalog (+1 because of the header)
			this.manualGasto.concepto = this.control.value;
      this.manualGasto.id = this.manualGasto.recurrente ? this.gastosRecurrentes.length + 1 : this.gastosOcasionales.length + 1;
    }

    // determine the row where to insert or update the info
    var rowIndexToUpdate = GastosComponentComponent.rangeGastosOcasionalesAddRow + (this.manualGasto.id > 0 ? (1 + this.manualGasto.id) : (2 + this.gastosOcasionales.length));
    if (this.manualGasto.recurrente){
      rowIndexToUpdate = GastosComponentComponent.rangeGastosRecurrentesAddRow + (this.manualGasto.id > 0 ? (1 + this.manualGasto.id) : (2 + this.gastosRecurrentes.length));
      this.manualGasto.montoReferencia = this.manualGasto.montoPagado;
    }

    const self = this;
    gapi.client.request(
      {
        method: 'POST',
        // Sets values in one or more ranges of a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/batchUpdate
        path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}/values:batchUpdate`, 
        body: {
          valueInputOption: AppComponent.valueInputOption,
          data: [
            {
              range: rowIndexToUpdate,
              values: [
                self.manualGasto.getRowForGastos()
              ]
            }
          ]
        }			
      }
    ).then(function(response)
      {
        self.ngZone.run( () =>
        {
          self.allGastos.push(self.manualGasto);
          if (self.manualGasto.recurrente){
            self.gastosRecurrentes.push(self.manualGasto);
          }else{
            self.gastosOcasionales.push(self.manualGasto);
          }

          // clean the new product information to enter a new one
          self.manualGasto = Gasto.createEmptyGasto();
          self.control.setValue("");
        });
      }
    );
	}
}