import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

import { GapiSession } from '../infrastructure/sessions/gapi.session';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { GastosComponentComponent } from './gastos-component/gastos-component.component';
import { LOCALE_ID } from '@angular/core';
import { registerLocaleData } from '@angular/common';
import localeCR from '@angular/common/locales/es-CR';

registerLocaleData(localeCR);

export function initGapi(gapiSession: GapiSession) {
  return () => gapiSession.initClient();
}

@NgModule({
  declarations: [
    AppComponent,
    GastosComponentComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
  FormsModule,
  ReactiveFormsModule,
  BrowserAnimationsModule,
  MatAutocompleteModule
  ],
  providers: [
	{ provide: APP_INITIALIZER, useFactory: initGapi, deps: [GapiSession], multi: true },
  GapiSession,
  {provide: LOCALE_ID, useValue: 'es-CR'}
  ],
  bootstrap: [AppComponent,GastosComponentComponent]
})
export class AppModule { 
  
}
